<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">{{ $t('ManageCategory') }}</h5>
                            <button v-on:click="Close" href="javascript:;" class="close">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <span class="user-guide">
                                <a class="ancuserguide" data-toggle="collapse" href="#collapseReply" role="button"
                                    aria-expanded="false" aria-controls="collapseExample">{{ $t('UserGuide') }}</a>
                                <div id="collapseReply" class="divancuserguide collapse">
                                    <div id="scrollbarmergeuserguide" class="custom-scrollbar-js">
                                        <div class="con" v-html="$t('UgAddManageCategory')"></div>
                                    </div>
                                </div>
                            </span>
                            <Form ref="AddManageCategoryModal">
                                <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema">
                                    <template v-slot:tgslot-Description="{ data }">
                                        <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value">
                                        </ckeditor>
                                        <span name="Description" class="invalid-feedback d-block">{{ data.error }}</span>
                                    </template>
                                    <template v-slot:tgslot-IsForServiceRequest="{ data }">
                                        <div class="form-group mb-0">
                                            <label class="switch">
                                                <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                                    v-model="data.fieldInfo.value" v-bind:name="'IsLogin'"
                                                    v-on:change="checkForAutoCreateEnable(data.fieldInfo.value)"
                                                    v-disabled="disabled">
                                                <span class="slider round" style="cursor:default;">
                                                    <span class="slider-yes">{{ $t('Yes') }}</span>
                                                    <span class="slider-no">{{ $t('No') }}</span>
                                                </span>
                                            </label>
                                        </div>
                                    </template>

                                </dynamic-form>
                            </Form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataServices from "@/services/DataService";
//import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
export default {
    props: {
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        }
    },
    data() {
        return {
            editor: this.CLASSIC_EDITOR,
            isLoading: false,
            isSaveAndClose: '',
            AutoServiceDisabled: true,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Category'),
                            name: "Category",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [{ value: '1001', name: 'Active' }, { value: '1003', name: 'Inactive' }],
                                showAddIcon: false
                            },
                            validationRules: { "required": false },
                        },

                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('IsForServiceRequest'),
                            name: "IsForServiceRequest",
                            value: "",
                            placeholder: "",
                            config: {
                                options: []
                            },
                            visibility: true,
                            validationRules: "",
                            settings: {
                                isInSlot: true
                            }
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 200 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save And Close",
                    value: "SaveAndClose",
                    onClick: this.SaveCategoryForm
                },
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.SaveCategoryForm

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    async created() {
        var vm = this;

    },
    methods: {
        checkForAutoCreateEnable: function (isChecked) {
            var vm = this;
            if (isChecked == true) {
                vm.AutoServiceDisabled = false;
            } else {
                vm.AutoServiceDisabled = true;
            }
        },
        Close: function () {
            var vm = this;
            vm.$parent.isManageCategory = false;
        },
        ClearFieldValue: function () {
            var vm = this;
            vm._data.FormSchema[0].Data[0].value = "";
            vm._data.FormSchema[0].Data[1].value = "";
            vm._data.FormSchema[1].Data[0].value = "";
            vm._data.FormSchema[2].Data[0].value = "";
        },
        SaveCategoryForm: function (btnType) {
            var vm = this;
            if (btnType.value == "SaveAndClose") {
                vm.isSaveAndClose = "SaveAndClose";
            }
            debugger;
            vm.$refs.AddManageCategoryModal.validate().then(result => {
                if (result) {
                    var jsonobj = {
                        CategoryName: vm._data.FormSchema[0].Data[0].value,
                        Status: vm._data.FormSchema[0].Data[1].value,
                        IsForServiceRequest: vm._data.FormSchema[1].Data[0].value,
                        Description: vm._data.FormSchema[2].Data[0].value,
                        CategoryType: 'TICKET_CATEGORY'
                    };
                    var params = JSON.stringify(jsonobj);
                    DataServices.SaveCategoryData(params).then((response) => {
                        debugger;
                        if (response.data.result.MSG == 'Success') {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Category")]), "success", true, vm.$t("Alert"));
                            if (vm.isSaveAndClose == "SaveAndClose") {
                                vm.Close();
                                if (vm.$parent.FormSchema.length > 0) {
                                    vm.$parent.FormSchema.forEach((group, gi) => {
                                        group.Data.forEach((field, fi) => {
                                            if (field.name == "ticket_category_id") {
                                                vm.callbackfunction('TICKET_CATEGORY', field, null);
                                            }
                                        });
                                    });
                                }

                            }
                            else {
                                vm.ClearFieldValue();
                                if (vm.$parent.FormSchema.length > 0) {
                                    vm.$parent.FormSchema.forEach((group, gi) => {
                                        group.Data.forEach((field, fi) => {
                                            if (field.name == "ticket_category_id") {
                                                vm.callbackfunction('TICKET_CATEGORY', field, null);
                                            }
                                        });
                                    });
                                }
                            }
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("Category")]), "failure", true, vm.$t("Alert"));
                        }
                    });
                }
            });
        }

    }
}
</script>