<template>
    <section class="main-content">
        <!-- <loader :is-visible="isLoading"></loader> -->
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white"> {{ $t('AddTicket') }}</span>
                    <span class="float-right">
                        <RouterLink to="/Ticketing/Index" class="p-action-btn text-white float-right">
                            <em class="fa fa-angle-double-left"></em>
                            <br />
                            {{ $t('BacktoList') }}
                        </RouterLink>
                    </span>
                </div>
            </div>
        </div>
        <div class="border p-3" v-if="isSuccessMessage">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12  ticket-thankyou text-center">
                <span class="checksign text-success"><i class="fa fa-check"></i></span>
                <h1 class="mt-4 mb-4">{{ $t('ThankYou') }}</h1>

                <div class="pb-4 ticket-number">
                    <P>{{ saveTicketStatus }}</P>
                    <div style="font-size: 20px;position: relative;background: #fff3cd;padding: 35px 30px; margin: 20px; border: 4px dotted #856404;display: inline-block;color: #856404;">
                        {{ TicketNumber }}</div>
                </div>
            </div>
        </div>
        <Form ref="AddTicket">
            <dynamic-form ref="AddTicketForm" :buttons="buttons" :schema="FormSchema" v-if="!isSuccessMessage">
                <template v-slot:tgslot-Description="{ data }">
                    <ckeditor :editor="editor" @ready="onReady" v-model="data.fieldInfo.value"
                        :class="{ 'is-invalid': data.error != '' }"></ckeditor>
                    <span class="invalid-feedback">{{ data.error }}</span>
                </template>
                <template v-slot:tgslot-addresFields="{ data }" v-if="isForServiceRequest">
                    <div class="row">
                        <div class="col-md-6">
                            <dynamic-form ref="AddTicketForm1" :schema="StaticObj"></dynamic-form>
                        </div>
                        <div class="col-md-6">
                            <ManageMap class="row" mapType="MapBox" mapContainer="WorkOrder" :config="config"
                                mapHeight="355px" v-if="isShowMap" @GetAddress="GetAddress"></ManageMap>
                        </div>
                    </div>
                </template>
            </dynamic-form>
        </Form>
        <ManageCategory v-if="isManageCategory" modulename="Ticketing" submodulecode="Ticketing"
            v-bind:callbackfunction="GetDropDownData"></ManageCategory>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import ManageMap from './../../components/Common/MapComponent.vue';
import ManageCategory from './DetailsComponent/AddManageCategoryModal.vue';
export default {
    components: {
        ManageMap,
        ManageCategory
    },
    data: function () {
        return {
            FormSchema: [],
            isSuccessMessage: false,
            fileMulitpleData: [],
            isShowMap: true,
            isForServiceRequest: false,
            StaticObj: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('ClientAddress'),
                            name: "AddrTo",
                            value: "",
                            disabled: false,
                            config: {
                                options: [],
                                onChange: this.onChangeDropdown

                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",

                        },
                    ]
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Address'),
                            name: "Address",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterAddress'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "country_id",
                            value: "",
                            disabled: true,
                            config: {
                                options: [],
                                onChange: this.GetStateDDL
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('State'),
                            name: "State",
                            value: "",
                            disabled: true,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('County'),
                            name: "County",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterCounty'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "City",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterCityName'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Street'),
                            name: "Street",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterStreet'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ZipCode'),
                            name: "Zip",
                            value: "",
                            disabled: true,
                            placeholder: this.$t('PleaseEnterZipCode'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('NoteAboutAddress'),
                            name: "Notes",
                            value: "",
                            disabled: false,
                            placeholder: this.$t('PleaseEnterDescription'),
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning save",
                    text: "<i class='fa fa-save pr-2'></i> Save As Draft",
                    value: "DRAFT",
                    onClick: this.saveForm
                },
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i> Submit",
                    onClick: this.saveForm
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i> Cancel",
                    onClick: this.Close
                }
            ],
            isManageCategory: false,
            editor: this.CLASSIC_EDITOR,
        }
    },
    created: function(){
        var vm = this;
        if (!vm.isBlank(vm.$route.params.id) && vm.$route.params.id != "0") {
            let ticket_id = decodeURIComponent(vm.$route.params.id);
            let ticket_type = vm.$route.params.ticket_Type;
            vm.getForm(ticket_id, ticket_type);
        }
        else {
            if (vm.$route.params.id == "0") {
                vm.getForm(0);

            }
            else {
                vm.$router.push({ name: "add", params: { id: 0 } });
                vm.getForm(0);
            }
        }
    },
    methods:{
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        onChangeEventForFields: function (x, y, z) {
            let ths = this;
            if(x == "FILL_DEPENDENT"){
                y.config.options = [];
                var url = `id=&moduleName=TICKETING&type=${y.config.fieldCode}&search=&refId=${z}`;
                DataServices.GetDDLData(url).then(function (response) {
                    if (response.data) {
                        var json = response.data;
                        if (json != null) {
                            y.config.options = response.data.DATA;
                            ths.$refs.AddTicketForm.UpdateKeyValue();
                        }
                    }
                });
            } else {
                if (y.name == "attachment") {
                    ths.GetBase64String(y.value);
                } else if (y.name == "ticket_category_id"){
                    let catOptions = ths.$refs.AddTicketForm.getPropertyValue("ticket_category_id", "config.options")
                    let getSelectedValue = catOptions.filter((x) => { return x.value ==  y.value})
                    ths.isForServiceRequest = getSelectedValue[0].first_value == "1" ? true : false;
                    if(ths.isForServiceRequest) {
                        ths.FormSchema.forEach(function(g, gi){
                            if(g.group_name == "Address"){
                                g.visibility = true;
                            }
                        })
                        ths.$refs.AddTicketForm.updateProperty("ticket_for", "value", "2");
                        ths.$refs.AddTicketForm.updateProperty("on_behalf", "value", "4");
                        ths.$refs.AddTicketForm.updateProperty("is_field_service", "value", "1");
                    } else {
                        ths.FormSchema.forEach(function(g, gi){
                            if(g.group_name == "Address"){
                                g.visibility = false;
                            }
                        })
                        ths.$refs.AddTicketForm.updateProperty("ticket_for", "value", "1");
                        ths.$refs.AddTicketForm.updateProperty("on_behalf", "value", "");
                        ths.$refs.AddTicketForm.updateProperty("is_field_service", "value", "0");
                    }
                }
            }
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                var f = evt[0]; // FileList object
                // var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                let fileObj = {};
                // Closure to capture the file information.
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64
                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;

                        fileObj.attachement_mime = attachemnetMime,
                            fileObj.fileName = fileName,
                            fileObj.base64String = base64StringEncoded,
                            fileObj.fileSize = f.size,
                            fileObj.contentType = f.type,
                            fileObj.ModuleName = 'TICKETING',
                            fileObj.SubModuleCode = 'TICKETING'
                        vm.fileMulitpleData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);

            }
            catch (ex) {
            }
        },
        getForm: function (id) {
            var ths = this;
            var url = `recordId=${id}&moduleName=Ticketing&subModuleCode=Ticketing`;
            DataServices.GetDynamicForm(url).then(function (response) {
                if (response != null && response.data != null) {
                    var responseData = response.data.data;
                    var frmSchma = []
                    // responseData.forEach((field, gi) => {
                    //     if (field.name == "assigned_to") {
                    //         let showJson = JSON.parse(field.show_when);
                    //         showJson.isFillOnly = true;
                    //         field.show_when = JSON.stringify(showJson);
                    //     }
                    // });
                    frmSchma = ths.$refs.AddTicketForm.ConvertCustomFieldIntoDynamicFormObj(responseData);
                    frmSchma.forEach((group, gi) => {
                        group.Data.forEach((field, fi) => {
                            if (field.name == "is_read") {
                                field.value = 0;
                            } else if (field.name == "attachment") {
                                field.showLabel = false;
                                field.config =
                                {
                                    maxSize: 5,
                                    accept: [
                                        {
                                            fileType: 'doc', // Document type
                                            iconClass: 'fa-file-word-o' // Icon class for documents
                                        },
                                        {
                                            fileType: 'docx', // Document type
                                            iconClass: 'fa-file-word-o' // Icon class for documents
                                        },
                                        {
                                            fileType: 'txt', // Text file type
                                            iconClass: 'fa-file-text-o' // Icon class for text files
                                        },
                                        {
                                            fileType: 'jpg', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'jpeg', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'png', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'gif', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'bmp', // Image types
                                            iconClass: 'fa-file-image-o' // Icon class for images
                                        },
                                        {
                                            fileType: 'mp4', // Video types
                                            iconClass: 'fa-file-video-o' // Icon class for videos
                                        },
                                        {
                                            fileType: 'mov', // Video types
                                            iconClass: 'fa-file-video-o' // Icon class for videos
                                        },
                                        // Add more image, document, text, and video types as needed
                                    ],
                                    //onChange: ths.GetBase64String,
                                    showAddIcon: false,
                                    onAddButtonClick: '',
                                    multiple: true
                                }
                            }
                            field.config.onChange = ths.onChangeEventForFields;
                        })
                    })
                    frmSchma.push({
                        layoutType: "single",
                        group_name: 'Address',
                        visibility: false,
                        Data: [{
                            astype: "SlotField",
                            name: "addresFields",
                            value: "",
                            visibility: true,
                            showLabel: false
                        }]
                    });
                    ths.FormSchema = frmSchma;
                }
            });
        },
        saveForm: function (btnType) {
            var ths = this;
            ths.$refs.AddTicket.validate().then(result => {
                if (result) {
                    if (btnType.value == "DRAFT") {
                        ths.FormSchema.forEach((group, gi) => {
                            group.Data.forEach((field, fi) => {
                                if (group.group_name == "General Information") {
                                    if (field.name == "TicketType") {
                                        field.value = 'DRAFT';
                                    }
                                }
                            });
                        });
                        ths.ticketType = 'DRAFT';
                    }
                    ths.isLoading = true;
                    var CustomFieldArray = ths.$refs.AddTicketForm.ConvertDynamicFormObjToCustomFieldObj(ths.FormSchema);

                    CustomFieldArray.forEach((x) => {
                        if (x.field_name == "on_behalf_consumer_id") {
                            if (!ths.isBlank(x.field_value)) {
                                x.field_value = x.field_value.value;
                            }
                        }
                        if (x.field_name == "work_type_id") {
                            if (!ths.isBlank(x.field_value)) {
                                x.field_value = x.field_value.value;
                            }
                        }
                        if (x.field_name == "cc_users") {
                            if (!ths.isBlank(x.field_value)) {
                                var CCUsers = '';
                                var i = 0;
                                for (i = 0; i < x.field_value.length; i++) {
                                    if (i == 0) {
                                        CCUsers = x.field_value[i].value;
                                    } else {
                                        CCUsers = CCUsers + "," + x.field_value[i].value;
                                    }
                                }
                                x.field_value = CCUsers;
                            }
                        }
                        if (x.field_name == "weekly") {
                            if (!ths.isBlank(x.field_value)) {
                                x.field_value = x.field_value.value;
                            }
                        }
                    })
                    // CustomFieldArrayStatic.forEach((x) => {
                    //     CustomFieldArray.push(x);
                    // });

                    var obj = {
                        data: CustomFieldArray,
                        moduleCode: "Ticketing",
                        subModuleCode: "Ticketing",
                        attachmentData: ths.fileMulitpleData.length > 0 ? ths.fileMulitpleData : null
                    }
                    DataServices.SaveDynamicForm(JSON.stringify(obj)).then(function (response) {
                        debugger;
                        ths.isLoading = false;
                        if (response != null && response.data != null && response.data[0].Status == "Success") {
                            ths.TicketNumber = JSON.parse(response.data[0].Value.OTHER_DATA).TICKET_NUMBER;
                            ths.ShowAlert(ths.$t("AddedSuccess", [ths.$t("Ticket")]), "success", true, ths.$t("Alert"));
                            ths.isSuccessMessage = true;
                            if (ths.ticketType == 'DRAFT') {
                                ths.saveTicketStatus = ths.$t("TicketDraftSubmitStatus");
                            }
                            else {
                                ths.saveTicketStatus = ths.$t("TicketSubmitStatus");
                            }
                        } else {
                            ths.ShowAlert(ths.$t("ErrorWhileSaving", [ths.$t("Ticket")]), "danger", true, ths.$t("Alert"));
                            ths.isSuccessMessage = false;
                        }
                    })
                }
            })
        }
    }
}
</script>